.CreateAndUpdateForm {
  display: grid;
  grid-template-columns: 1fr;
  align-content: center;
  margin: 2rem auto;
  padding: 1rem;
  background-color: #333333;
  box-shadow: 0.5rem 0.5rem #e1e1e1;
  border-radius: 1vh;
  width: 100%;
  max-width: 60rem;
  box-sizing: border-box;
}
.CreateAndUpdateForm form {
  display: grid;
  grid-template-columns: 1fr;
  font-size: 1.5rem;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .CreateAndUpdateForm form {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.CreateAndUpdateForm form .UploadImage {
  text-align: center;
  color: #FFFFFF;
}
.CreateAndUpdateForm form .UploadImage .ImagenPreview {
  width: 100%;
  height: 25rem;
  object-fit: contain;
  margin: 1rem 0;
}
.CreateAndUpdateForm form .UploadImage .FileUpload {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 1rem;
}
.CreateAndUpdateForm form .UploadImage .FileUpload input[type=file] {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: auto;
  cursor: pointer;
}
.CreateAndUpdateForm form .UploadImage .FileUpload .CustomFileUpload {
  padding: 1rem;
  text-align: center;
  background-color: #e74c3c;
  color: white;
  border-radius: 2vh;
  transition: background-color 0.5s;
  width: 100%;
  cursor: pointer;
}
.CreateAndUpdateForm form .UploadImage .FileUpload .CustomFileUpload:hover {
  background-color: #d62c1a;
}
.CreateAndUpdateForm form .FormFields,
.CreateAndUpdateForm form .QuoteFormFields {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.CreateAndUpdateForm form .FormFields label,
.CreateAndUpdateForm form .QuoteFormFields label {
  font-size: 1.5rem;
  color: #FFFFFF;
  font-weight: bold;
}
.CreateAndUpdateForm form .FormFields input,
.CreateAndUpdateForm form .FormFields textarea,
.CreateAndUpdateForm form .QuoteFormFields input,
.CreateAndUpdateForm form .QuoteFormFields textarea {
  width: 100%;
  padding: 1rem;
  font-size: 1.5rem;
  border: none;
  outline: none;
  border-radius: 2vh;
  box-sizing: border-box;
  background-color: #e1e1e1;
  color: #000000;
}
.CreateAndUpdateForm form .FormFields textarea,
.CreateAndUpdateForm form .QuoteFormFields textarea {
  resize: none;
  height: 6rem;
}
@media (min-width: 1200px) {
  .CreateAndUpdateForm form .FormFields textarea,
  .CreateAndUpdateForm form .QuoteFormFields textarea {
    height: 12rem;
  }
}
@media (min-width: 1680px) {
  .CreateAndUpdateForm form .FormFields textarea,
  .CreateAndUpdateForm form .QuoteFormFields textarea {
    height: 16rem;
  }
}
.CreateAndUpdateForm form button {
  width: 100%;
  color: #FFFFFF;
  background-color: #16a085;
  border: none;
  font-size: 1.5rem;
  font-weight: bold;
  padding: 1rem;
  cursor: pointer;
  transition: border-radius 0.5s, background-color 0.5s;
  margin-top: 1rem;
}
.CreateAndUpdateForm form button:hover {
  border-radius: 2vh;
  background-color: #107360;
}

.SectionBlog {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-content: center;
  max-width: 80rem;
  margin: 1rem auto;
  padding: 1rem;
}
.SectionBlog .BlogArticle {
  margin-bottom: 2rem;
}
.SectionBlog .BlogArticle:last-of-type {
  margin-bottom: 5rem;
}
.SectionBlog .BlogArticle img {
  width: 100%;
  height: auto;
}
