  margin: 1rem auto;
  padding: 1rem;
  box-shadow: 0px 0.5rem 0.5rem #e1e1e1;
  border-radius: 2vh;
}

form {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  font-family: "dream-orphans";
}

fieldset {
  border: 1px solid #e1e1e1;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 8px;
}

legend {
  font-weight: bold;
  color: #333333;
}

label {
  display: block;
  margin-top: 10px;
  font-weight: bold;
}

input,
textarea,
select {
  width: 100%;
  margin-top: 1rem;
  padding: 0.5rem 0rem 0.5rem 0.5rem;
  border: 1px solid #e1e1e1;
  border-radius: 5px;
  font-size: 16px;
}

textarea {
  resize: vertical;
  min-height: 100px;
}

.propertyImages {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  background-color: #e1e1e1;
  padding: 1rem;
}
@media (min-width: 768px) {
  .propertyImages {
    grid-template-columns: repeat(2, 1fr);
  }
}
.propertyImages .propertyItem {
  text-align: center;
  padding: 0.5rem;
  transition: 1s;
  cursor: pointer;
}
.propertyImages .propertyItem img {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .propertyImages .propertyItem img {
    width: 100%;
    height: 10rem;
    object-fit: contain;
  }
}

.SelectedProperty {
  background-color: #122a29;
  color: #FFFFFF;
}

.ContactMethod {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.ContactMethod div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.ContactMethod .InputContacts {
  width: 100%;
}

.greenbutton {
  background-color: #122a29;
  color: #efb810;
  padding: 10px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.5s;
}

.greenbutton:hover {
  background-color: #0a1818;
}